<template>
    <el-form :inline="true" :model="formTitle" label-width="70px">   
            <el-form-item label="">
                <el-input v-model="formTitle.phone" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="formTitle.relation" placeholder="">
                    <el-option 
                        v-for='item in relationList'
                        :label="item.name" 
                        :value="item.name"
                        :key="item.value"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="formTitle.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="formTitle.record_status" placeholder="">
                    <el-option 
                        v-for='item in endList'
                        :label="item.name" 
                        :value="item.value"
                        :key="item.value"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-input type="textarea" v-model="formTitle.remark" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-date-picker
                    v-model="formTitle.next_time"
                    type="datetime"
                    value-format='yyyy-MM-dd HH:mm:ss'
                    placeholder="">
                </el-date-picker>
            </el-form-item>
        </el-form>
</template>
<script>
export default {
    name:'formCaseDetail',
    props:[
        'formTitle',
        'relationList',
        'endList'
    ],
    }
</script>



