<template>
    <el-table
        border
        :data="tableSecond"
        style="width: 98%"
        >
        <el-table-column
        label=""
        prop="name">
        </el-table-column>
        <el-table-column
        label=""
        prop="relation">
        </el-table-column>
        <el-table-column
        label=""
        prop="phone">
        </el-table-column>
        <el-table-column
        label=""
        prop="creater">
        </el-table-column>
        <el-table-column
        label=""
        prop="create_time">
        </el-table-column>
        <el-table-column
        label=""
        prop="type">
            <template slot-scope='scope'>
                <span v-show='scope.row.type==1'> </span>
                <span v-show='scope.row.type==2'> </span>
                <span v-show='scope.row.type==3'> </span>
                <span v-show='scope.row.type==4'> </span>
                <span v-show='scope.row.type==6'> </span>
            </template>
        </el-table-column>
        <el-table-column
        label=""
        prop="type"
        >
            <template slot-scope='scope' >
                <el-button  :disabled ='scope.row.type != 6 && scope.row.type != 1' type="primary" size='mini' @click="checkFn(scope.row)" ></el-button>
            </template>
        </el-table-column>
        <el-table-column
        prop="staff_id"
        label=""
        >
        </el-table-column>
        <el-table-column
        label=""
        prop="remark">
        </el-table-column>
        <el-table-column
        label=""
        prop="case_id_num">
            <template slot-scope='scope'>
                <el-button type="success" size='mini' @click="addRemarkFn(scope.row)" ></el-button>
            </template>
        </el-table-column>
    </el-table>
</template>
<script>
export default {
    name:'tableCaseSecond',
    props:[
        'tableSecond'
    ],
    methods:{
        checkFn(column){
            this.$emit('checkFn',column)
        },
        addRemarkFn(column){//
            this.$emit('addRemarkFn',column)
        }
    }
}
</script>




